}

.add_to_cart {
  padding: 10px 20px;
  background-color: green;
  color: white;
  margin-left: 15px;
  margin-top: 100px;
}

.photos {
  margin-top: 100px;
}

.desc {
  margin-top: 20px;
}

.submit {
  margin-top: 10px;
  margin-bottom: 10px;
}

.form {
  margin: 5% 25%;
  box-sizing: border-box;
  padding-left: 22%;
}

.cart_container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.ind_cart_prod {
  display: inline-flex;
  justify-content: center;
  align-items: center;
}

.total_price {
  margin-top: 50px;
}

.checkout {
  background-color: rgb(16, 211, 16);
  margin: 50px 0;
  padding: 10px 20px;
}

.border_image {
  border: 3px solid green;
  width: 100px;
  margin: 10px;
}

